import React from "react";
import TechChip from "../techChip/TechChip";
import "./ProjectCard.css";
import { Fade } from "react-reveal";
import { tagIcons } from "./Project";
import { IconButton } from "@mui/material";
import { ClickAwayListener } from "@mui/material";
import { Tooltip as MUITooltip } from '@mui/material';
import { useState } from "react";


const getDateRangeStr = (dateRange) => {
  let firstStr
  let secondStr

  if (dateRange.length === 1) { // Case 1: Ongoing
    firstStr = dateRange[0].toLocaleString({ month: 'short', year: 'numeric' })
    secondStr = 'present'
  } else if (
    dateRange[0].year === dateRange[1].year &&
    dateRange[0].month === dateRange[1].month
  ) { // Case 2: One-month
    return dateRange[0].toLocaleString({ month: 'short', year: 'numeric' })
  } else {
    firstStr = dateRange[0].year === dateRange[1].year ?
      dateRange[0].toLocaleString({ month: 'short' }) : // Case 3: Same year
      dateRange[0].toLocaleString({ month: 'short', year: 'numeric' }) // Case 4: Different year
    secondStr = dateRange[1].toLocaleString({ month: 'short', year: 'numeric' }) // Case 3 and 4
  }

  return `${firstStr} - ${secondStr}`
}

export default function ProjectCard({ project, theme }) {
  function openRepoinNewTab(url) {
    var win = window.open(url, "_blank");
    win.focus();
  }

  return (
    <div>
      <Fade bottom duration={2000} distance="40px">
        <div
          className="repo-card-div"
          key={project.id}
          // onClick={() => openRepoinNewTab(project.url)} // If include an onclick, uncomment the pointer css
          style={{ backgroundColor: theme.highlight }}
        >
          <div style={{ display: "flex", justifyContent: "center", margin: "0.75rem 0" }}>
            {project.tags.map((tag) =>
              <Tooltip
                title={tag}
                style={{ marginRight: "0.25rem", cursor: "pointer" }}
                key={`${project.id}-${tag}`}
              >
                {tagIcons[tag]}
              </Tooltip>
            )}
          </div>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <div style={{ minWidth: "75%", marginRight: "8px" }}>
              <div className="repo-name-div" style={{ flex: 1 }}>
                <div className="repo-name" style={{ color: theme.text }}>
                  {project.title}
                </div>
                {!!project.links &&
                  project.links.map((link, index) =>
                    <IconButton onClick={() => openRepoinNewTab(link)} key={`link-${index}`}>
                      <i className="fas fa-link"></i>
                      {/* <i className="fas fa-external-link-alt"></i> */}
                    </IconButton>
                  )
                }
                {!!project.github && project.github !== "" &&
                  <IconButton onClick={() => openRepoinNewTab(project.github)}>
                    <i className="fab fa-github"></i>
                  </IconButton>
                }
              </div>
              <p className="repo-subtitle" style={{ color: theme.text }}>
                {project.subtitle}<br />[{getDateRangeStr(project.date)}]
              </p>
            </div>
            {!!project.image &&
              <div
                onClick={() => openRepoinNewTab(project.image)}
                style={{ cursor: "pointer" }}
              >
                <img
                  src={project.image}
                  alt={`img-${project.id}`}
                  style={{ display: "inline-block", maxWidth: project.imageMaxWidth || "100%" }}
                />
              </div>
            }
          </div>


          {project.points.map((point, index) =>
            <div style={{ textAlign: "left", marginBottom: "0.1rem", fontSize: "1rem" }} key={`${project.id}-${index}`}>
              - {point}
            </div>
          )}
          <div style={{ display: "flex", justifyContent: "flex-end", marginTop: "1rem" }}>
            {project.tech.map((tech) =>
              <div style={{ marginRight: "0.25rem" }} key={`${project.id}-${tech}`}>
                <TechChip tech={tech} />
              </div>
            )}
          </div>
        </div>
      </Fade >
    </div >
  );
}


const Tooltip = (props) => {
  const { children, ...muiTooltipProps } = props
  const [tooltipOpen, setTooltipOpen] = useState(false);

  return (
    <ClickAwayListener onClickAway={() => setTooltipOpen(false)}>
      <div>
        <MUITooltip
          {...muiTooltipProps}
          PopperProps={{
            disablePortal: true,
          }}
          open={tooltipOpen}
          onClose={() => setTooltipOpen(false)}
          disableFocusListener
          disableHoverListener
          disableTouchListener
        >
          <div onClick={() => setTooltipOpen(true)}> {children}</div>
        </MUITooltip>
      </div>
    </ClickAwayListener>
  )
}
