import React from "react";
import Chip from '@mui/material/Chip';
import "./TechPill.css";

const BaseTechChip = ({ title, textColour, bkgColour, isActive = true, onClick }) => (
    isActive ?
        <Chip
            label={title}
            // color="primary"
            sx={{ color: textColour, backgroundColor: bkgColour }}
            onClick={onClick}
            clickable={false} // Disables the hover colour
        /> :
        <Chip
            label={title}
            color="success"
            variant="outlined"
            onClick={onClick}
        />
)


const TechChip = ({ tech, isActive = true, onClick }) => (
    Object.keys(ALL_TECH).includes(tech.toLowerCase()) ?
        <BaseTechChip
            title={ALL_TECH[tech.toLowerCase()].title}
            textColour={ALL_TECH[tech.toLowerCase()].textColour}
            bkgColour={ALL_TECH[tech.toLowerCase()].bkgColour}
            isActive={isActive}
            onClick={onClick}
        /> :
        <BaseTechChip
            title={tech}
            textColour="white"
            bkgColour="black"
            isActive={isActive}
            onClick={onClick}
        />
)

