import React from "react";
import read_img from "../../assets/images/green/undraw_reading_time_gvg0.svg"
import trends_img from "../../assets/images/green/undraw_Data_report_re_p4so.svg"
import world_img from "../../assets/images/green/undraw_world_9iqb.svg"
import skills_img from "../../assets/images/green/undraw_product_iteration_kjok.svg"
import inclusion_img from "../../assets/images/green/undraw_collab_8oes.svg"
import team_img from "../../assets/images/green/undraw_collab_8oes.svg"

const IMG_SOURCES = {
    read: read_img,
    trends: trends_img,
    skills: skills_img,
    world: world_img,
    team: team_img,
    inclusion: inclusion_img,
}

const Img = ({name}) => (
    <img src={IMG_SOURCES[name]} alt="" />
)

export default Img;