import React, { useState, useEffect } from "react";
import Header from "../../components/header/Header";
import Footer from "../../components/footer/Footer";
import Button from "../../components/button/Button";
import TopButton from "../../components/topButton/TopButton";
import { Fade } from "react-reveal";
import { projectsHeader } from "../../portfolio.js";
import "./Projects.css";
import ProjectsImg from "./ProjectsImg";
import ProjectCard from "../../components/projectCard/ProjectCard";
import { getProjectsByTag, getProjectsByTech, projectDateSort, TAG, tagIcons } from "../../components/projectCard/Project";

import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';
import TechChip, { ALL_TECH, TECH_CATEGORIES } from "../../components/techChip/TechChip";

import styled from "styled-components";

const FILTER_TYPE = { // TODO: Change this after typescript is enabled
  tag: "Show by Tag",
  tech: "Show by Tech"
}

const Projects = (props) => {
  const theme = props.theme;
  // TODO: Make this into an enum-like type
  const [currTag, setCurrTag] = useState(TAG.favourite);
  const [currFilterType, setCurrFilterType] = useState(FILTER_TYPE.tag);
  const [currTechs, setCurrTechs] = useState([]);
  const [techChipActivities, setTechChipActivities] = useState(
    Object.fromEntries(Object.keys(ALL_TECH).map((tech) => [tech, false]))
  );

  const handleFilterChoice = (event, newFilterType) => {
    if (!!newFilterType) setCurrFilterType(newFilterType);
  };
  const handleTagChoice = (event, newTag) => {
    if (!!newTag) setCurrTag(newTag);
  };
  const getHandleTechChoiceOnClick = (techChoice) => (() => {
    setTechChipActivities(
      {
        ...techChipActivities,
        [techChoice]: !techChipActivities[techChoice]
      }
    )
  })

  // Project key changes every time the filter (either tag or tech) changes so that
  // card refades back in
  const getProjectKey = (project) => `${currTag}-${currTechs.length}-${project.id}`

  useEffect(() => {
    setCurrTechs(
      Object.keys(techChipActivities).filter((tech) => techChipActivities[tech])
    )
  }, [techChipActivities])

  return (
    <div className="projects-main">
      <Header theme={theme} />
      <div className="basic-projects">
        <Fade bottom duration={2000} distance="40px">
          <div className="projects-heading-div">
            <div className="projects-heading-img-div">
              <ProjectsImg theme={theme} />
            </div>
            <div className="projects-heading-text-div">
              <h1
                className="projects-heading-text"
                style={{ color: theme.text }}
              >
                {projectsHeader.title}
              </h1>
              <p
                className="projects-header-detail-text subTitle"
                style={{ color: theme.secondaryText }}
              >
                {projectsHeader["description"]}
              </p>
              <Fade duration={2000} collapse when={currFilterType === FILTER_TYPE.tag}>
                <div>
                  {Object.keys(TECH_CATEGORIES).map((category) => (
                    <div key={category}>
                      {TECH_CATEGORIES[category].map((chipName) => (
                        <TechChip tech={chipName} key={`${category}-${chipName}`} />
                      ))}
                    </div>
                  ))}
                </div>
              </Fade>
            </div>
          </div>
        </Fade>
      </div>
      <FilterSection>
        <ToggleButtonGroup
          color="secondary"
          value={currFilterType}
          exclusive
          onChange={handleFilterChoice}
          style={{ marginBottom: "16px", marginTop: "24px" }}
        >
          {Object.values(FILTER_TYPE).map((filterType) =>
            <ToggleButton value={filterType} key={filterType}>
              {filterType}
            </ToggleButton>
          )}
        </ToggleButtonGroup>
        {currFilterType === FILTER_TYPE.tag &&
          <Fade duration={2000} distance="40px">
            <ToggleButtonGroup
              color="primary"
              value={currTag}
              exclusive
              onChange={handleTagChoice}
            >
              <ToggleButton value="All">All</ToggleButton>
              {Object.keys(tagIcons).map((tag) =>
                <ToggleButton value={tag} key={tag} disableRipple>
                  <div style={{ display: "flex", flexDirection: "column" }}>
                    {tag}
                    {tagIcons[tag]}
                  </div>
                </ToggleButton>
              )}
            </ToggleButtonGroup>
          </Fade>
        }
        {currFilterType === FILTER_TYPE.tech &&
          <Fade duration={2000} distance="40px">
            {Object.keys(TECH_CATEGORIES).map((category) => (
              <div key={category}>
                {TECH_CATEGORIES[category].map((chipName) => (
                  <TechChip
                    tech={chipName}
                    key={`${category}-${chipName}`}
                    isActive={techChipActivities[chipName]}
                    onClick={getHandleTechChoiceOnClick(chipName)}
                  />
                ))}
              </div>
            ))}
          </Fade>
        }
      </FilterSection>
      <div className="repo-cards-div-main">
        {/* {ProjectsData.data.map((repo) => {
            return <GithubRepoCard repo={repo} theme={theme} />;
          })} */}
        {currFilterType === FILTER_TYPE.tag &&
          getProjectsByTag(currTag)
            .sort(projectDateSort)
            .map((project) => {
              return <ProjectCard project={project} theme={theme} key={getProjectKey(project)} />;
            })
        }
        {currFilterType === FILTER_TYPE.tech && (
          currTechs.length > 0 ?
            getProjectsByTech(currTechs)
              .sort(projectDateSort)
              .map((project) => {
                return <ProjectCard project={project} theme={theme} key={getProjectKey(project)} />;
              }) :
            <div style={{ marginBottom: "50px" }}>
              <span role="img" aria-label="">  </span>
              Please select at least one technology above to display the projects
              <span role="img" aria-label="">  </span>
            </div>
        )}
      </div>
      <Button
        text={"My Github"}
        className="project-button"
        href="https://github.com/TheHong"
        newTab={true}
        theme={theme}
      />

      {/* {// Publications
          PublicationData.data.length > 0 &&
          <>
            <div className="basic-projects">
              <Fade bottom duration={2000} distance="40px">
                <div className="publications-heading-div">
                  <div className="publications-heading-text-div">
                    <h1
                      className="publications-heading-text"
                      style={{ color: theme.text }}
                    >
                      {publicationsHeader.title}
                    </h1>
                    <p
                      className="projects-header-detail-text subTitle"
                      style={{ color: theme.secondaryText }}
                    >
                      {publicationsHeader["description"]}
                    </p>
                  </div>
                </div>
              </Fade>
            </div>
            <div className="repo-cards-div-main">
              {PublicationData.data.map((repo) => {
                return <PublicationCard repo={repo} theme={theme} />;
              })}
            </div>
          </>
        } */}
      <Footer theme={props.theme} onToggle={props.onToggle} />
      <TopButton theme={props.theme} />
    </div>
  );
}


export default Projects;

const FilterSection = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`