import React from "react";
import { projects } from "../../portfolio.js";


export const projectDateSort = (p1, p2) => {
    if (p1.date.length === 1 && p2.date.length === 1) {// Case 1: Both are ongoing
        // Then the older one is the one with older start date
        return p2.date[0] - p1.date[0]
    } else if (p1.date.length === 1) { // Case 2A: p1 is ongoing
        return -1
    } else if (p2.date.length === 1) { // Case 2B: p2 is ongoing
        return 1
    } else { // Case 3: None are ongoing
        return p2.date[1] - p1.date[1]
    }
}

// TAG ========================================================================
export const getProjectsByTag = (tag) => (
    projects.filter((project) => (tag === "All") || project.tags.includes(tag))
);

export const TAG = {
    favourite: "Favourites",
    aiml: "AI/ML",
    cloud: "Cloud",
    web: "Web",
    mobile: "Mobile",
    robo: "Robotics",
    data: "Data Analysis",
}

export const TAG_ID = Object.fromEntries(Object.values(TAG).map((tag, index) => [tag, index]));

export const tagIcons = {
    [TAG.favourite]: <i className="fas fa-star" style={{ color: "#ff922b", }}></i>,
    [TAG.aiml]: <i className="fas fa-brain" style={{ color: "#339af0" }}></i>,
    [TAG.cloud]: <i className="fas fa-cloud" style={{ color: "#51d7e9" }}></i>,
    [TAG.web]: <i className="fas fa-desktop" style={{ color: "#51cf66" }}></i>,
    [TAG.mobile]: <i className="fas fa-mobile-alt" style={{ color: "#845ef7" }}></i>,
    [TAG.robo]: <i className="fas fa-robot" style={{ color: "black" }}></i>,
    [TAG.data]: <i className="fas fa-database" style={{ color: "#ff018c" }}></i>,
}

