import React, { Component } from "react";
import "./Header.css";
import { Fade } from "react-reveal";
import { NavLink, Link } from "react-router-dom";
import * as Info from "../../portfolio.js";
import SeoHeader from "../seoHeader/SeoHeader";

const onMouseEnter = (event, color) => {
  const el = event.target;
  el.style.backgroundColor = color;
};

const onMouseOut = (event) => {
  const el = event.target;
  el.style.backgroundColor = "transparent";
};

class Header extends Component {
  render() {
    const theme = this.props.theme;
    console.log(theme);
    const link = Info.settings.isSplash ? "/splash" : "home";

    const NavItem = ({ text, to }) => (
      <li>
        <NavLink
          to={to}
          tag={Link}
          activeStyle={{ fontWeight: "bold" }}
          style={{ color: theme.text }}
          onMouseEnter={(event) => onMouseEnter(event, theme.highlight)}
          onMouseOut={(event) => onMouseOut(event)}
        >
          {text}
        </NavLink>
      </li>
    )

    return (
      <Fade top duration={1000} distance="20px">
        <SeoHeader />
        <div>
          <header className="header">
            <NavLink to={link} tag={Link} className="logo">
              {/* TODO: Add logo */}
              {/* <span style={{ color: theme.text }}> &lt;</span>
              <span className="logo-name" style={{ color: theme.text }}>
                {Info.greeting.logo_name}
              </span>
              <span style={{ color: theme.text }}>/&gt;</span> */}
              Chi-Hong Cheung
            </NavLink>
            <input className="menu-btn" type="checkbox" id="menu-btn" />
            <label className="menu-icon" htmlFor="menu-btn">
              <span className="navicon"></span>
            </label>
            <ul className="menu" style={{ backgroundColor: theme.body }}>
              <NavItem to="/home" text="Home" />
              {/* <NavItem to="/education" text="Education" /> */}
              {/* <NavItem to="/experience" text="Experience" /> */}
              <NavItem to="/projects" text="Projects" />
              {/* <NavItem to="/opensource" text="Open Source" /> */}
              {/* <NavItem to="/contact" text="Contact Me" /> */}
            </ul>
          </header>
        </div>
      </Fade>
    );
  }
}


export default Header;
